import React from "react";
import { HeaderWrapper, LeftSide, RightSide, ArrowLeft, ArrowRight } from "./styled";
import { IconButton } from '../../../basic/icon-button';

interface IHeaderProps {
  children: React.ReactNode;
  onPrev?: () => void;
  onNext?: () => void;
  className?: string;
}

export const Header: React.FC<IHeaderProps> = React.memo((props) => {
  const { children, onPrev, onNext, className } = props;

  return (
    <HeaderWrapper className={className}>
      <LeftSide>{children}</LeftSide>
      <RightSide>
        {onPrev && <IconButton icon={<ArrowLeft />} onClick={onPrev}/>}
        {onNext && <IconButton icon={<ArrowRight/>} onClick={onNext}/>}
      </RightSide>
    </HeaderWrapper>
  );
});
